"use client"

import { useState } from "react"
import { ArrowRight } from "lucide-react"
import QuoteModal from "./quote-modal"

export default function CTABanner() {
  const [isQuoteModalOpen, setIsQuoteModalOpen] = useState(false)
  return (
    <section className="relative py-24 md:py-32 bg-slate-900 flex items-center justify-center overflow-hidden">
      {/* Background Image */}
      <div
        className="absolute inset-0"
        style={{
          backgroundImage: "url(/stainless-steel-bar.jpg)",
          backgroundSize: "cover",
          backgroundPosition: "center",
        }}
      />
      
      {/* Dark Overlay */}
      <div className="absolute inset-0 bg-slate-900/70"></div>

      {/* Content */}
      <div className="relative z-10 max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
        <h2 className="text-4xl md:text-6xl font-bold text-white mb-8 leading-tight">
          Outline Your World IN A WHOLE NEW WAY.
        </h2>
        <button 
          onClick={() => setIsQuoteModalOpen(true)}
          className="bg-blue-600 hover:bg-blue-700 text-white px-8 py-4 rounded font-semibold flex items-center gap-2 transition mx-auto"
        >
          GET STARTED TODAY!
          <ArrowRight className="w-5 h-5" />
        </button>
      </div>
      <QuoteModal 
        open={isQuoteModalOpen} 
        onClose={() => setIsQuoteModalOpen(false)}
        source="CTA Banner"
      />
    </section>
  )
}

